import React, { Component } from 'react';
import { withRouter, Switch, Redirect, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import { authvalidation } from './actions/pns';
import Header from './views/components/Header';
import Dashboard from './views/components/Dashboard';
import Profile from './views/components/Profile';
import Footer from './views/components/Footer';
import ComponentLoading from './views/components/ComponentLoading';
import Unauthorized from './views/pages/Unauthorized';

const renderingContent = (props)=> {
  let _content = [];
  _content.push((
    <Switch>
      <Route path="/dashboard" name="Dashboard" component={Dashboard} />
      <Route path="/profile" name="Profile" component={Profile} />
      <Route path="/oops" name="Unauthrozied" component={Unauthorized} />
      <Redirect from="/" to="/dashboard" />
    </Switch>
  ))
  if (props.onAppLoading.status) {
    _content.push(<ComponentLoading message={props.onAppLoading.message.message} title={props.onAppLoading.message.title} />);
  }
  if (props.onnewtoken.status) {
    _content.push((
      <img src={"http://e-penerjemahan-sso.peraturan.go.id/sso/setcookie?value=" + props.onnewtoken.value} style={{ display: 'none' }} />
    ));
  }
  return _content;
}

const authenticationChecking = (props)=> {
  if (props.isAuthenticated) {
      return (
        <div>
          <Header />
          <div className="container" style={{ paddingTop: '150px' }}>
            { renderingContent(props) }
          </div>
          <Footer />
        </div>
      );
  }
  return <Redirect to="/login" />
}

class App extends Component {

  componentDidMount() {
    this.props.authvalidation();
  }

  render() {
    return (
      <div>
        { authenticationChecking(this.props) }
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    isAuthenticated: state.isAuthenticated,
    onAppLoading: state.apiProcess.isLoading.app,
    onnewtoken: state.newtoken,
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
    authvalidation: ()=> dispatch(authvalidation())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));



