import states from '../reducers/statesInventory'

export function onLoading (type, status, message = '', key = '') {
  return {
    type: type,
    isLoading: {status, message, key}
  }
}

export function hasError (type, status, message = '', key = '') {
  return {
    type: type,
    hasError: {status, message, key}
  }
}

export function onupdatetokendata (pns, allowedApplication) {
  return {
    type: states.onupdatetokendata,
    tokendata: {pns, allowedApplication}
  }
}

export function onSuccess (status, message = '', key = '') {
  return {
    type: states.isSuccess,
    isSuccess: {status, message, key}
  }
}

export function onAuthenticating (isAuthenticated) {
  return {
    type: states.isAuthenticated,
    isAuthenticated: isAuthenticated
  }
}

export function onnewtoken (status, value = '') {
  return {
    type: states.onnewtoken,
    newtoken: {status, value}
  }
}



