import React, {Component} from "react";
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux';


const rendermessage = (message)=> {
  if (message == null) {
    return ('Anda tidak memiliki <b>authorisasi</b> pada layanan ini.');
  } else {
    return message;
  }
}

const renderview = (me)=> {
  if (!me.props.onAppLoading.status) {
    return (
      <div className="unauthorized row align-items-center justify-content-md-center">
        <div className="col-lg-9 col-sm-4 mb-3">
          <span style={{fontSize: 100, fontWeight: "bold"}}>Maaf,</span> <br />
          <span style={{fontSize: 40}}>{ rendermessage(me.props.message) }</span>
        </div>
        <div className="col-lg-3 col-sm-8 text-center mb-3">
          <img className="applicationimage rounded-circle d-block mx-auto" src={'img/unauthorized-logo.jpg' } width="180px" height="180px" style={{ marginTop: 50}}/>
        </div>
      </div>
    );
  } else {
    return null;
  }
}


class Unauthorized extends Component {
  render() {
    return (
      <div>
        { renderview(this) }
      </div>
    )
  }
}

const mapStateToProps = (state)=> {
  return {
    onAppLoading: state.apiProcess.isLoading.app,
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
  }
}


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Unauthorized));



