import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import InputFormValidation from './InputFormValidation'
import { hasError, onSuccess } from '../../actions/statesChanging'
import states from '../../reducers/statesInventory'

const rendervalue = (me)=> {
  if (me.state.isedit) {
    return (
      <div className="col-sm-8">
        <InputFormValidation input={{
          type: 'text',
          isformcontrol: true,
          name: me.props.cellkey,
          placeholder: me.props.placeholder,
          class: 'col-sm-12',
          existingvalue: me.state[me.props.cellkey]
        }} events={{
          onchange: (e)=> handleonchangeeditedvalue(me, e)
        }} />
      </div>
    );
  } else {
    return (
      <div className="col-sm-8">
        <span> { me.props.value } </span>
      </div>
    );
  }
}

const renderbutton = (me)=> {
  if (me.props.onformloading.status && me.props.onformloading.key === me.props.cellkey) {
    return (<button type="button" className="btn btn-info" disabled>{ me.props.onformloading.message }</button>);
  } else if (me.state.isedit) {
    return (
      <div className="btn-group col-sm-2" role="group">
        <button disabled={me.props.onformloading.status ? true : false} type="button" className={ 'btn btn-success ' + clasisshowbutton(me) } onClick={()=> {me.props.onsubmit(me.state[me.props.cellkey])}} ><FontAwesomeIcon icon="check"/></button>
        <button type="button" onClick={()=> handlebuttonclick(me)} className={ 'btn btn-danger ' + clasisshowbutton(me) }><FontAwesomeIcon icon="times" /></button>
      </div>
    );
  } else {
    return (
      <div className="col-sm-2">
        <button type="button" onClick={ ()=> handlebuttonclick(me) } className={ 'btn btn-primary ' + clasisshowbutton(me, true) }><FontAwesomeIcon icon="edit" /></button>
      </div>
    );
  }
}

const clasisshowbutton = (me, reverse)=> {
  if (reverse) {
    return (me.state.isedit) ? 'collapse' : 'show';
  }
  return (me.state.isedit) ? 'show' : 'collapse';
}

const handlebuttonclick = (me)=> {
  me.props.makenoerror();
  me.props.makenosuccess();
  const _newstate = {
    isedit: !me.state.isedit,
    [me.props.cellkey]: me.props.value
  }
  me.setState(_newstate);
}

const handleonchangeeditedvalue = (me, e)=> {
  const _newvalue = e.target.value;
  me.setState({
    [me.props.cellkey]: _newvalue
  })
}

class EditableCell extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isedit: false,
    };
  }

  render() {

    return (
      <div className="col-sm-12 row align-items-center">
        { rendervalue(this) }
        { renderbutton(this) }
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    hasError: state.apiProcess.hasError,
    onformloading: state.apiProcess.isLoading.form,
    onSuccess: state.apiProcess.isSuccess
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
    makenoerror: ()=> dispatch(hasError(states.hasFormError, false)),
    makenosuccess: ()=> dispatch(onSuccess(false))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditableCell));



