export function updateprofile (data, key = '') {
  return (dispatch)=> {
    dispatch(onLoading(states.onFormLoading, true, 'Memperbaharui ...', key));
    dispatch(onSuccess(false));
    dispatch(hasError(states.hasFormError, false))
    updateprofileapi(data, (err, res)=> {
      mainCallback(dispatch, err, res, (err, res)=> {
        setTimeout(()=> {
          dispatch(onLoading(states.onFormLoading, false));
          if (res.code === 201) {
            dispatch(onupdatetokendata(res.pns, res.allowedapplications));
            dispatch(onSuccess(true, res.message, res.key));
          } else {
            dispatch(hasError(states.hasFormError, true, res.message, res.key))
          }
        }, 500);
      })
    })
  }
}

/**
 * main callback to handle the global callback of API
 * @param  {[type]} dispatch    [description]
 * @param  {[type]} err         [description]
 * @param  {[type]} res         [description]
 * @param  {[type]} newcallback [description]
 * @return {[type]}             [description]
 */
function mainCallback(dispatch, err, res, newcallback) {
  if (res.code === 401) {
    dispatch(onLoading(states.onAppLoading, true, {title: "Session Anda Berakhir, Silahkan Login Kembali", message: "Server sedang membersihkan data anda, mohon tunggu ..."}));
    localStorage.clear();
    cookie.remove('kemenkumhamssoengine', {
      domain: 'kemenkumham.go.id'
    })
    setTimeout(()=> {
      dispatch(onLoading(states.onAppLoading, false))
      dispatch(onAuthenticating(null, {}))
    }, 1000)
  } else {
    if (res.newtoken != null) {
      cookie.set('kemenkumhamssoengine', res.newtoken, {
        domain: 'kemenkumham.go.id'
      })
      dispatch(onnewtoken(true, res.newtoken));
    }
    newcallback(err, res)
  }
}



