import React from 'react'

export default class ComponentLoading extends React.Component {
  render() {
    return (
      <div className="heightonehundredpercent">
        <div className="loading">
          <div className="loading-bar" />
          <div className="loading-bar" />
          <div className="loading-bar" />
          <div className="loading-bar" />
        </div>
        <div className={((this.props.classText) ? this.props.classText : '') + " loadingText"}>
          <h4>{ this.props.title ? this.props.title : ''  }</h4>
          <p>{ this.props.message ? this.props.message : '' }</p>
        </div>
      </div>
    )
  }
}



