import React, {Component} from "react";
import { withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Cookie from 'universal-cookie'
const cookie = new Cookie();

class Setcookie extends Component {
  componentDidMount() {
    const _token = this.props.location.search.split("?value=");
    cookie.set('kemenkumhamssoengine', _token[1], {
      domain: 'kemenkumham.go.id'
    })
  }

  render() {
    return (
      <div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Setcookie));



