import React, {Component} from "react";
import { withRouter, Redirect } from 'react-router-dom';
import { logPnsIn } from '../../actions/pns';
import { connect } from 'react-redux';
import '../../assets/css/login.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import InputFormValidation from '../components/InputFormValidation';
import Cookie from 'universal-cookie';
const cookie = new Cookie();

const authenticationChecking = (props)=> {
  if (props.isAuthenticated) {
    return <Redirect from='/login' to='/dashboard' />
  }
  return null
}

const handleloginchange = (me, e)=> {
  const _name = e.target.name;
  const _value = e.target.value;
  me.setState(prevState=> ({
    loginform: Object.assign({}, prevState.loginform, {
      [_name]: _value
    })
  }))
}

const handleOnSubmit = (e, me) => {
  e.preventDefault();
  me.props.logPnsIn(me.state.loginform);
}

const handlesetcookie = (me)=> {
  if (me.props.onnewtoken.status) {
    return (
      <img src={"http://e-penerjemahan-sso.peraturan.go.id/sso/setcookie?value=" + me.props.onnewtoken.value} style={{ display: 'none' }} />
    );
  }
  return null;
}

const handleInformation = (me)=> {
  if (me.props.hasError.form.status) {
    if (me.props.hasError.form.key === 'all') {
      return (
        <div style={{'fontSize': '12px'}} className="alert alert-danger">
          <FontAwesomeIcon className="fai-margin fai-lg" icon="user-times" /> {me.props.hasError.form.message}
        </div>
      )
    } else {
      return null
    }
  } else if (me.props.isSuccess.status) {
    return (
      <div style={{'fontSize': '12px'}} className="alert alert-success">
        <FontAwesomeIcon className="fai-margin fai-lg" icon="user-check" /> {me.props.isSuccess.message};
      </div>
    )
  } else {
    return null
  }
}

const handleOnFormLoading = (props)=> {
  let _span, _isdisabled;
  if (props.isLoading.form.status) {
    _span = props.isLoading.form.message
    _isdisabled = true
  } else {
    _span = 'Login'
    _isdisabled = false
  }
  return <input className={(_isdisabled ? "authenticating" : "signin") } type="submit" disabled={ _isdisabled} value={_span} />
}

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loginform: {}
    }
  }
  render() {
    return (
      <div className="body align-items-center row justify-content-md-center">
      { authenticationChecking(this.props)}
      { handlesetcookie(this) }
        <div className="container">
          <div className="content">
            <div className="row no-padding">
              <div className="col-md-8 kumham">
                <img src={'img/sso.png'} className="logosso" alt="logo"/>
                <div className="infologin">
                  <div className="infologinbox">
                    <p className="infologintext">Teknologi yang mengizinkan <br /> <span style={{ textTransform: 'uppercase' }}>pengguna jaringan </span> agar dapat <span style={{ textTransform: 'uppercase' }}>mengakses </span> <br /> sumber daya hanya dengan menggunakan <span style={{ textTransform: 'uppercase' }}><br />satu akun</span></p>
                  </div>
                </div>
                <p className="versionlogin">v.1.0</p>
                <p className="copyrightlogin"> 2018</p>
              </div>
              <div className="col-md-4 form">
                <form onSubmit={(e)=> handleOnSubmit(e, this)}>
                  <div className="konten">
                    <div className="text-center" style={{ minHeight: 60 + 'px' }}>
                      { handleInformation(this) }
                    </div>
                    <h3 className="login">Login</h3>
                    <label className="field field_animated field_a2 page__field">
                      <InputFormValidation input={{
                        type: 'text',
                        isformcontrol: false,
                        name: 'authorization',
                        placeholder: 'NIP / Username',
                        class: 'field__input'
                      }} events={{
                        onchange: (e)=> handleloginchange(this, e)
                      }} />
                    </label>
                    <label className="field field_animated field_a2 page__field">
                      <InputFormValidation input={{
                        type: 'password',
                        isformcontrol: false,
                        name: 'password',
                        placeholder: 'Password',
                        class: 'field__input'
                      }} events={{
                        onchange: (e)=> handleloginchange(this, e)
                      }} />
                    </label><br />
                    <div className="col-xs-12">
                      { handleOnFormLoading(this.props) }
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
      </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    isLoading: state.apiProcess.isLoading,
    hasError: state.apiProcess.hasError,
    isSuccess: state.apiProcess.isSuccess,
    isAuthenticated: state.isAuthenticated,
    onnewtoken: state.newtoken
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logPnsIn: (data) => dispatch(logPnsIn(data)),
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));



