import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Application from './Application';
import Unauthorized from '../pages/Unauthorized'
import '../../assets/css/dashboard.css';

const renderingApplications = (props)=> {
  if (props.onAppLoading.status) {
    return null;
  } else {
    if (props.applications.length > 0) {
        let _applications = [];
        props.applications.forEach((item, index)=> {
          _applications.push(<Application data={item} />)
        })
        return _applications;
    } else if (!props.onAppLoading.status) {
      return (
        <Unauthorized message={'Anda tidak memiliki authorisasi terhadap aplikasi Kementerian Hukum dan Hak Asasi Manusia.'}/>
      );
    }
  }
}

class Dashboard extends Component {

  render() {
    return (
      <div className="row dashboard content justify-content-md-center">
        { renderingApplications(this.props) }
        <div className="row ghost"></div>
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    applications: state.tokendata.allowedApplication,
    onAppLoading: state.apiProcess.isLoading.app
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Dashboard));



