import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

const renderformcontrolclass = (me)=> {
  let _formcontrolclass = me.props.input.class;
  if (me.props.input.isformcontrol) {
      _formcontrolclass += ' form-control ';
  }
  if (me.props.hasError.form.status && me.props.hasError.form.key === me.props.input.name) {
    _formcontrolclass += ' is-invalid ';
  }
  if (me.props.isSuccess.status && me.props.isSuccess.key === me.props.input.name) {
    _formcontrolclass += 'is-valid ';
  }
  return _formcontrolclass;
}

const renderonchange = (e, me)=> {
  if (me.props.events.onchange == null) {
    return (
      ()=> {}
    );
  }
  return (
    me.props.events.onchange(e)
  );
}

const renderinputform = (me)=> {
  return (
    <input type={ me.props.input.type } className={ renderformcontrolclass(me) } name={ me.props.input.name } placeholder={ me.props.input.placeholder }  onChange={ (e)=> renderonchange(e, me) } value={ me.props.input.existingvalue } />
  );
}

const rendervalidationinfo = (me)=> {
  if (me.props.hasError.form.status && me.props.hasError.form.key === me.props.input.name) {
    return (
      <div className="invalid-feedback">
        { me.props.hasError.form.message }
      </div>
    );
  }
    if (me.props.isSuccess.status && me.props.isSuccess.key === me.props.input.name) {
      return (
        <div className="valid-feedback">
        { me.props.isSuccess.message }
        </div>
      );
    }
  return null;
}

class InputFormValidation extends Component {
  render() {
    return (
      <div className="form-row">
        {renderinputform(this)}
        {rendervalidationinfo(this)}
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    hasError: state.apiProcess.hasError,
    isSuccess: state.apiProcess.isSuccess
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(InputFormValidation));



