import {post, get, url} from './config';
import Cookie from 'universal-cookie';

const cookie = new Cookie();

/**
 * default Header of GET method to connect API
 * @return {[type]} [description]
 */
function authenticatedFeaturesGET () {
  get.headers['kemenkumham-ssoauthorization'] = cookie.get('kemenkumhamssoengine') ? cookie.get('kemenkumhamssoengine') : null;
  get.headers['application'] = 'dashboard';
}

/**
 * default Header of POST method to connect API
 * @return {[type]} [description]
 */
function authenticatedFeaturesPOST () {
  post.headers['kemenkumham-ssoauthorization'] = cookie.get('kemenkumhamssoengine') ? cookie.get('kemenkumhamssoengine') : null;
  post.headers['application'] = 'dashboard';
}

/**
 * Login PNS to API
 * @param  {[array]}   data     data required as login parameter
 *    @param {[string]}   authorization     nip or username
 *    @param {[string]}   password          password input by user
 * @param  {Function} callback [description]
 * @return {[type]}            [description]
 */
export function login (data, callback) {
  post.body = JSON.stringify(data);
  return fetch(url + 'pns/auth/login', post)
    .then(response=> {
      response.json().then(json=> {
        callback(false, json);
      })
    }).catch(()=> callback(true, 'Silahkan hubungi PUSDATIN.'))
}

/**
 * Log PNS out the system
 * @param  {Function} callback [description]
 * @return {[type]}            [description]
 */
export function logout (callback) {
  authenticatedFeaturesPOST();
  return fetch(url + 'pns/auth/logout', post)
    .then(response=> {
      response.json().then(json=> {
        callback(false, json);
      })
    }).catch(()=> callback(true, 'Silahkan hubungi PUSDATIN.'))
}

/**
 * get the entire allowed applications provided to user
 * @param  {Function} callback [description]
 * @return {[type]}            [description]
 */
export function getAllowedApplications (callback) {
  authenticatedFeaturesGET();
  return fetch(url + 'pns/getallowedapplications', get)
    .then(response=> {
      response.json().then(json=> {
        callback(false, json);
      })
    }).catch(()=> callback(true, 'Silahkan hubungi PUSDATIN.'))
}

export function updateprofile (data, callback) {
  authenticatedFeaturesPOST();
  post.body = JSON.stringify(data);
  return fetch(url + 'pns/profile/update', post)
    .then(response=> {
      response.json().then(json=> {
        callback(false, json);
      })
    }).catch(()=> callback(true, 'Silahkan hubungi Pusdatin'));
}

export function authvalidation (callback) {
  authenticatedFeaturesGET();
  return fetch(url + 'pns/auth/validation', get)
    .then(response=> {
      response.json().then(json=> {
        callback(false, json);
      })
    }).catch(()=> callback(true, 'Silahkan hubungi Pusdatin'));
}



