import React, {Component} from 'react';
import { withRouter, Redirect, Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { logPnsOut } from '../../actions/pns'
import '../../assets/css/header.css';

const redirectprofile = (me)=> {
  if (me.state.isprofile) {
    me.setState(prevState=> ({
      isprofile: false
    }));
    return <Redirect to="/profile" />
  }
}

const classshowdropdown= (me)=> {
  return (
    'dropdown-menu' + (me.state.isToggleOn ? ' show' : '')
  );
}

const renderpnsdata = (me, key)=> {
  return (me.props.pnsdata[key]);
}

const handleprofileclick = (e, me)=> {
  me.setState(prevState=> ({
    isToggleOn: !prevState.isToggleOn,
    isprofile: true
  }))
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isToggleOn: false,
      isprofile: false
    };
  }
  showDropdown(e) {
    e.preventDefault();
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn
    }));
  }

  render() {

    return (
      <nav className="navbar navbar-expand navbar-dark bg-dark fixed-top">
        { redirectprofile(this) }
        <div id="menuToggle">
          <input type="checkbox" />
          <span />
          <span />
          <span />
          <ul id="menu">
            <img className="userimg rounded-circle d-block mx-auto" src={'img/userimg.png'} width="60px" height="60px" />
            <div className="userinfo">
                <p className="username">{ renderpnsdata(this, 'name') }</p>
                <p className="usernip">{ renderpnsdata(this, 'nip') }</p>
              </div>
            <button className="dropdown-item" onClick={ (e)=> handleprofileclick(e, this) }> <FontAwesomeIcon className="fai-margin fai-lg" icon="users-cog" /> Profile </button>
            <button className="dropdown-item" onClick={ ()=> { this.props.logout() } }>
              <FontAwesomeIcon className="fai-margin fai-lg" icon="sign-out-alt" /> Log Out
            </button>

          </ul>
        </div>
        <div className="row align-items-center">
          <Link className="navbar-brand" to="/">
            <img className="navbrandimage" src={'img/sso.png'} />
            <div className="containertitle">
              <p className="navtitle navtitlesso">Single Sign On (SSO)</p>
              <p className="navtitle navtitlekemenkumham">Kementerian Hukum dan Hak Asasi Manusia RI</p>
            </div>
          </Link>
          <div className="navbar-nav ml-md-auto">
            <div className="mobile">
              <img className="userimg rounded-circle d-block mx-auto" src={'img/userimg.png'} width="60px" height="60px" />
              <div className="userinfo">
                <span className="username">{ renderpnsdata(this, 'name') }</span> <br />
                <span className="usernip">{ renderpnsdata(this, 'nip')  }</span>
              </div>
              <div className="usermenu dropdown">
                <button className="btn btn-dark" id="dropdownMenuButton" onClick={(e)=> this.showDropdown(e) } >
                  <FontAwesomeIcon icon="ellipsis-v" />
                </button>
                <div className={ classshowdropdown(this) }>
                  <button className="dropdown-item" onClick={ (e)=> handleprofileclick(e, this) }> <FontAwesomeIcon className="fai-margin fai-lg" icon="users-cog" /> Profile </button>
                  <button className="dropdown-item" onClick={(e)=> { this.showDropdown(e); this.props.logout() }}><FontAwesomeIcon className="fai-margin fai-lg" icon="sign-out-alt" /> Log Out</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    pnsdata: state.tokendata.pns,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logout:()=> dispatch(logPnsOut())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header))



