import React, {Component} from 'react';
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

const classshowdropdownapplication = (me)=> {
  return (
    'dropdown-menu' + (me.state.isToggleOn ? ' show' : '')
  );
}

const handleClickApp = (e, data)=> {
  e.preventDefault();
  if (data.isintegrated) {
    window.open((data.url + 'sso/' + data.accounts[0].username), "_blank");
  } else {
    alert(data.name + ' belum terintegrasi.');
  }
}

const handlechooseapplication = (e, showDropdown, url) => {
  showDropdown(e);
  window.open(url);
}

const displayaccounts = (accounts, url, showDropdown)=> {
  let _accountsview = [];
  accounts.forEach((account, index)=> {
    _accountsview.push(
      <button className="dropdown-item" onClick={(e)=> handlechooseapplication(e, showDropdown, (url + 'sso/' + account.username)) }> { account.name }</button>
    )
  });
  return _accountsview;
}

const handleListApplication = (data, me, showDropdown)=> {
  if (data.accounts != null) {
    if (data.accounts.length > 1) {
      return (
        <div>
          <div className="useraccounts dropdown">
            <div id="dropdownobject">
              <img className="applicationimage rounded-circle d-block mx-auto" onClick={(e)=> showDropdown(e)} src={'img/' + data.image } width="120px" height="120px" />
              <h4> { data.name }</h4>
            </div>
            <div className={ classshowdropdownapplication(me) }>
              {displayaccounts(data.accounts, data.url, showDropdown)}
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <a className="applicationitem" onClick={(e)=> handleClickApp(e, data)}>
            <img className="applicationimage rounded-circle d-block mx-auto" src={'img/' + data.image } width="120px" height="120px" />
            <h4> { data.name }</h4>
          </a>
        </div>
      );
    }
  } else {
    return (
      <div>
        <a className="applicationitem" onClick={(e)=> handleClickApp(e, data)}>
          <img className="applicationimage rounded-circle d-block mx-auto" src={'img/' + data.image } width="120px" height="120px" />
          <h4> { data.name }</h4>
        </a>
      </div>
    );
  }
}

class Application extends Component {

  constructor(props) {
      super(props);
      this.state = {
        isToggleOn: false
      };
    }
    showDropdown(e, me) {
      e.preventDefault();
      me.setState(prevState => ({
        isToggleOn: !prevState.isToggleOn
      }));
    }

  render() {
    return (
      <div className="col-lg-2 col-sm-3 text-center mb-3 enabledapplication">
       { handleListApplication(this.props.data, this, (e)=> this.showDropdown(e, this))}
      </div>
    )
  }
}

export default withRouter(connect(null, null)(Application))



