import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import EditableCell from './EditableCell'
import { updateprofile } from '../../actions/pns'
import InputFormValidation from './InputFormValidation'
import '../../assets/css/profile.css';

const editabledata = [{
  label: 'Name',
  key: 'name'
}, {
  label: 'Username',
  key: 'username'
}]


const rendereditablerow = (me)=> {
  let _editabledata = [];
  editabledata.forEach((item, index)=> {
    _editabledata.push(
      <tr className="row align-items-center">
        <th className="col-md-3">{ item.label }</th>
        <td className="col-md-9 align-items-center row">
          <EditableCell value={ renderinputvalue(me, item.key) } onsubmit={(data)=> handleupdateprofile(me, item.key, data) } cellkey={ item.key } placeholder={ item.label + ' Anda' } />
        </td>
      </tr>
    );
  });
  return _editabledata;
}

const handleupdateprofile = (me, key, data)=> {
  const _reqbody = {
    key: key,
    updatedvalue: data
  }
  me.props.updateprofile(_reqbody, key);
}

const renderinputvalue = (me, key)=> {
  return (me.props.pnsdata[key]);
}

const handleclickcollapse = (me)=> {
  me.setState(prevState=> ({
    iscollapseshow: !prevState.iscollapseshow,
  }))
}

const handlepasswordformsubmit = (e, me)=> {
  e.preventDefault();
  let _data = me.state.passwordform;
  _data.key = 'password';
  me.props.updateprofile(_data, 'password');
}

const handleOnFormLoading = (me)=> {
  let _span, _isdisabled;
  if (me.props.isLoading.form.status && me.props.isLoading.form.key === 'password') {
    _span = me.props.isLoading.form.message;
    _isdisabled = true;
  } else {
    _span = 'Ganti';
    _isdisabled = false;
  }
  return (
    <div>
      <button onClick={ ()=> handleclickcollapse(me) } type="button" className="btn btn-danger float-right">Batal</button>
      <button className="btn btn-success float-right" type="submit" disabled={ _isdisabled}>{_span}</button>
    </div>
  );
}

const handlepasswordonchange = (me, e)=> {
  const _name = e.target.name;
  const _value = e.target.value;
  me.setState(prevState=> ({
    passwordform: Object.assign({}, prevState.passwordform, {
      [_name]: _value
    })
  }))
}

const handleInformation = (me)=> {
  if (me.props.hasError.form.status) {
    if (me.props.hasError.form.key === 'all') {
      return (
        <div style={{'fontSize': '12px'}} className="text-center alert alert-danger">
          <FontAwesomeIcon className="fai-margin fai-lg" icon="user-times" /> {me.props.hasError.form.message}
        </div>
      )
    } else {
      return null
    }
  } else if (me.props.onSuccess.status && me.props.onSuccess.key === 'password') {
    return (
      <div style={{'fontSize': '12px'}} className="text-center alert alert-success">
        <FontAwesomeIcon className="fai-margin fai-lg" icon="user-check" /> {me.props.onSuccess.message}
      </div>
    )
  } else {
    return null
  }
}

const classshowcollapse = (me)=> {
  return (
    "collapse row " +( me.state.iscollapseshow ? 'show' : '' )
  );
}

const renderview = (me)=> {
  if (!me.props.onAppLoading.status) {
    return (
      <div  className="row profile content justify-content-center align-items-center">
        <div className="col-md-4 col-sm-12">
          <img className="userimg rounded-circle d-block mx-auto" src={'img/userimg.png'} width="200px" height="200px" />
        </div>
        <div className="col-md-8 col-sm-12 textentity">
          <table className="table">
            <tbody>
              { rendereditablerow(me) }
            </tbody>
          </table>
          <div className="row">
            <button className="btn btn-primary" type="button" onClick={()=> handleclickcollapse(me)}>
              Ganti Password
            </button>
            <div className={ classshowcollapse(me) }>
              <div className="card card-body">
                <form onSubmit={ (e)=> handlepasswordformsubmit(e, me) }>
                  { handleInformation(me) }
                  <div className="form-group row align-items-center">
                    <label className="col-sm-3 col-form-label">Old Password</label>
                    <div className="col-sm-9">
                      <InputFormValidation input={{
                        type: 'password',
                        isformcontrol: true,
                        name: 'oldpassword',
                        placeholder: 'Password Anda saat ini.',
                        class: 'col-sm-12'
                      }} events={{
                        onchange: (e)=> handlepasswordonchange(me, e)
                      }} />
                    </div>
                  </div>
                  <div className="form-group row align-items-center">
                    <label className="col-sm-3 col-form-label">New Password</label>
                    <div className="col-sm-9">
                      <InputFormValidation input={{
                        type: 'password',
                        isformcontrol: true,
                        name: 'newpassword',
                        placeholder: 'Password baru Anda.',
                        class: 'col-sm-12',
                      }} events={{
                        onchange: (e)=> handlepasswordonchange(me, e)
                      }} />
                    </div>
                  </div>
                  <div className="form-group row align-items-center">
                    <label className="col-sm-3 col-form-label">Confirm Password</label>
                    <div className="col-sm-9">
                      <InputFormValidation input={{
                        type: 'password',
                        isformcontrol: true,
                        id: 'confirmpassword',
                        name: 'confirmpassword',
                        placeholder: 'Ulangi Password baru Anda.',
                        class: 'col-sm-12',
                      }} events={{
                        onchange: (e)=> handlepasswordonchange(me, e)
                      }} />
                    </div>
                    <div className="col-sm-12">
                      { handleOnFormLoading(me) }
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  return null;
}


class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      iscollapseshow: false,
      passwordform: {}
    }
  }
  render() {
    return (
      <div>
        { renderview(this) }
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    onAppLoading: state.apiProcess.isLoading.app,
    pnsdata: state.tokendata.pns,
    onSuccess: state.apiProcess.isSuccess,
    hasError: state.apiProcess.hasError,
    isLoading: state.apiProcess.isLoading,
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
    updateprofile: (data, key)=> dispatch(updateprofile(data, key))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Profile));



