import { combineReducers } from 'redux'
import states from './statesInventory'
import initialState, { token, secretJWT } from './initialState'
import Cookie from 'universal-cookie'
import jwt from 'jsonwebtoken';

const cookie = new Cookie();

export default combineReducers({
  apiProcess,
  isAuthenticated,
  tokendata,
  newtoken
})

function apiProcess(state = initialState, action) {
  switch (action.type) {
    case states.onFormLoading:
      return Object.assign({}, state, {
        isLoading: {
          form : action.isLoading,
          app: {status: false, message: ''}
        }
      })
    case states.onAppLoading:
    return Object.assign({}, state, {
      isLoading: {
        form : {status: false, message: ''},
        app: action.isLoading
      }
    })
    case states.hasFormError:
      return Object.assign({}, state, {
        hasError: {
          form : action.hasError
        }
      })
    case states.isSuccess:
      return Object.assign({}, state, {
        isSuccess: action.isSuccess
      })
    default:
      return initialState.apiProcess
  }
}

function isAuthenticated (state = initialState, action) {
  switch (action.type) {
    case states.isAutheticated:
      return action.isAuthenticated;
    default:
      return localStorage.getItem('isAuthenticated') !== null && cookie.get('kemenkumhamssoengine') != null;
  }
}

function tokendata (state = initialState, action) {
  switch (action.type) {
    case states.onupdatetokendata:
      return action.tokendata
    default:
      return cookie.get('kemenkumhamssoengine') != null ? jwt.decode(cookie.get('kemenkumhamssoengine'), secretJWT) : {pns: {}, allowedApplication: []};
  }
}

function newtoken (state = initialState, action) {
  switch (action.type) {
    case states.onnewtoken:
      return action.newtoken
    default:
      return initialState.newtoken;
  }
}



