import React, {Component} from 'react';
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

class Footer extends Component {
  render() {
    return (
      <footer className="navbar fixed-bottom navbar-dark bg-dark">
        <div className="container">
          <div>
            <p className="m-0 text-center text-white" style={{ fontSize: '12px' }}>Pusdatin - Sekretariat Jenderal, Kementerian Hukum dan Hak Asasi Manusia RI  2018</p>
          </div>
          <div className="ml-md-auto pusdatin">
            <img src={'img/pusdatin.png'} width="120px" height="auto" />
          </div>
        </div>
      </footer>
    )
  }
}

export default withRouter(connect(null, null)(Footer))



