import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route, Switch} from 'react-router-dom';
import {createBrowserHistory} from 'history';
import {Provider} from 'react-redux';
import configureStore from './stores/config';

import App from './App';
import Login from './views/pages/Login'
import Setcookie from './views/pages/Setcookie'

import '../node_modules/bootstrap/dist/css/bootstrap.min.css';

import './assets/css/index.css';
import './assets/css/loading.css';
import './assets/css/round-about.css';
import './assets/css/footer.css';



import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUserCheck, faUserTimes, faEllipsisV, faUsersCog, faSignOutAlt, faEdit, faCheck, faTimes } from '@fortawesome/free-solid-svg-icons';

library.add(faUserTimes);
library.add(faUserCheck);
library.add(faEllipsisV);
library.add(faUsersCog);
library.add(faSignOutAlt);
library.add(faEdit);
library.add(faCheck);
library.add(faTimes);

const history = createBrowserHistory();
const store = configureStore();

ReactDOM.render((
  <Provider store={store}>
    <HashRouter history={history}>
      <Switch>
        <Route exact path="/login" name="LoginPage" component={Login} />
        <Route exact path="/setcookie" name="SetcookieEngine" component={Setcookie} />
        <Route path="/" name="Home" component={App} />
      </Switch>
    </HashRouter>
  </Provider>
), document.getElementById('root'));



